import {mapState,mapGetters,mapActions,mapMutations} from 'vuex'
import {INCREMENT,MSG} from '../vuex/mutation-types';

export default {
	name:'user',
	data(){
		return{
			tel:'未登录',
			name:'未登录',
			dd:[{name:"待付款",img:'../img/2.jpg'},{name:'待收货',img:'../img/2.jpg'},{name:'已完成',img:'../img/2.jpg'}],
			xjd:[{name:'已报价',img:'../img2.jpg'},{name:"待报价",img:'../2.jpg'}],
			gg:[{name:"发票管理",img:'../img/2.jpg'},{name:'收货地址',img:'../img/2.jpg'},{name:'修改密码',img:'../img/2.jpg'},{name:"关于震坤行",img:'../img/2.jpg'}]
		}
	},
	methods:{
		handletc(){
			localStorage.removeItem('login');
			this.$router.push({path:'/login'})
		}
	},
	mounted(){
		
		var xx=JSON.parse(localStorage.getItem("login"))
		if(xx){
			this.tel=xx.tel
			this.name=xx.name
		}
		
	},
	computed:{
	
	}
}



